export type UserType = 'craftsperson' | 'client' | 'admin';
export type RegistrationStatus = 'partial' | 'complete';

export interface CrowdCraftUser {
    id: string;
    username: string;
    userType: UserType;
    firstName: string;
    lastName: string;
    country: string;
    registrationStatus: RegistrationStatus;
    isServiceAccount: boolean;
    serviceAccountId?: string;
}

export interface CrowdCraftAuthInfo {
    token: string;
}

declare module 'express' {
    export interface Request {
        user?: User | undefined;
        authInfo?: AuthInfo | undefined;
    }
    export interface User extends CrowdCraftUser {}
    export interface AuthInfo extends CrowdCraftAuthInfo {}
}
